
.box-item
    margin-bottom: 1.5rem
    padding: 1rem
    border-radius: 5px!important
    .box-header
        margin-bottom: 1rem !important
        .box-header-txt h1
            font-size: 1.05rem
        .box-icon
            padding: 0.1rem //0.2rem
            width: 30px
            height: 30px
            border: 1px solid $tc-blue-dark-1
            border-radius: 50px
            transition: transform .1s linear
            overflow: hidden
            &:hover img
                transform: scale(1.3)
            img
                transition: transform .3s linear
                width: 100%
                position: relative
    .box-body
        padding-left: 1.1rem !important
        p
            font-size: 1rem
